<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="steelfoam.meta_semana"
    :progress="steelfoam.progress"
    :name="steelfoam.nombre"
    :depto="steelfoam.depto"
    :days="steelfoam.dias_laborados"
    :m3="steelfoam.m3_persona"
    :bonus_p="steelfoam.bono_persona"
    :bonus_tot="steelfoam.bono_depto"
    :asis="steelfoam.asistencia"
    :extra="steelfoam.$_extra_m3"
    :bonus_prod="steelfoam.bono_productividad"
    :bonus_goals="steelfoam.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Steelfoam',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'steelfoam',
            userCode: this.$route.params.user,
            city: 'veracruz'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        steelfoam() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>